import React, { useState, useEffect } from 'react';
import { Link, useNavigate, useLocation } from 'react-router-dom';
import { projectAPI } from '../../utils/api';
import './ProjectList.css';

const ProjectList = ({ user }) => {
  const [projects, setProjects] = useState([]);
  const [loading, setLoading] = useState(true);
  const navigate = useNavigate();
  const location = useLocation();

  // 
  const currentUser = user || {
    username: sessionStorage.getItem('username'),
    address: sessionStorage.getItem('address')
  };

  useEffect(() => {
    const currentUserInfo = {
      username: sessionStorage.getItem('username'),
      address: sessionStorage.getItem('address'),
      token: sessionStorage.getItem('token')
    };
    
    loadProjects();
    
    // 
    if (location.state?.shouldRefresh) {
      if (location.state.message) {
        setTimeout(() => alert(location.state.message), 100);
      }
      // 
      navigate('/projects', { replace: true });
    }
  }, [location.state, navigate, currentUser]);

  const loadProjects = async () => {
    try {
      setLoading(true);

      // projectAPI.myProjects()
      const response = await projectAPI.myProjects();
      
      if (response.ok) {
        setProjects(response.data || []);
        
      } else {
        console.error(':', response.error);
      }
    } catch (error) {
      console.error(':', error);
    } finally {
      setLoading(false);
    }
  };

  const handleRefresh = () => {
    loadProjects();
  };

  const handleDeleteProject = async (projectId, projectName, e) => {
    e.preventDefault(); // Link
    e.stopPropagation(); // 
    
    if (!window.confirm(`"${projectName}"`)) {
      return;
    }

    try {
      const response = await projectAPI.delete(projectId);
      if (response.ok) {
        alert('');
        loadProjects(); // 
      } else {
        alert(`: ${response.error?.message || ''}`);
      }
    } catch (error) {
      console.error(':', error);
      alert('');
    }
  };

  if (loading) {
    return <div className="loading">...</div>;
  }

  return (
    <div className="project-list">
      <div className="project-list-header">
        <h1></h1>
        <div className="header-actions">
          <button 
            className="refresh-btn"
            onClick={handleRefresh}
            title=""
          >
             
          </button>
          <button 
            className="create-project-btn"
            onClick={() => navigate('/project/create')}
          >
            + 
          </button>
        </div>
      </div>

      <div className="projects-grid">
        {projects.map(project => (
          <div key={project.projectId} className="project-card-wrapper">
            <Link 
              to={`/project/${project.projectId}`} 
              className="project-link"
            >
              <div className="project-card">
                <div className="project-header">
                  <h3>{project.projectName}</h3>
                  <span className="project-status">{project.status || ''}</span>
                </div>
                <p className="project-description">{project.description || ''}</p>
                <div className="project-meta">
                  <span>: {project.projectOwner}</span>
                </div>
              </div>
            </Link>
            
            {/*  -  */}
            {user && user.username === project.projectOwner && (
              <button
                className="delete-project-btn"
                onClick={(e) => handleDeleteProject(project.projectId, project.projectName, e)}
                title=""
              >
                
              </button>
            )}
          </div>
        ))}
      </div>

      {projects.length === 0 && !loading && (
        <div className="no-projects">
          <h3></h3>
          <p></p>
          <button 
            className="create-first-project-btn"
            onClick={() => navigate('/project/create')}
          >
            
          </button>
        </div>
      )}
    </div>
  );
};

export default ProjectList;




