"use client"

import { useState } from "react"
import { useNavigate } from "react-router-dom"
import { projectAPI } from "../../utils/api"
import "./Project.css"

export default function ProjectCreate({ user }) {
  const navigate = useNavigate()
  const [name, setName] = useState("")
  const [description, setDescription] = useState("")
  const [startDate, setStartDate] = useState("")
  const [endDate, setEndDate] = useState("")
  const [errors, setErrors] = useState({})
  const [submitting, setSubmitting] = useState(false)
  const [loading, setLoading] = useState(false)
  const [error, setError] = useState("")

  const validate = () => {
    const e = {}
    if (!name.trim()) e.name = ""
    if (startDate && endDate && startDate > endDate) e.time = ""
    setErrors(e)
    return Object.keys(e).length === 0
  }

  const handleSubmit = async (e) => {
    e.preventDefault();
    
    if (!validate()) return;
    
    const token = sessionStorage.getItem('token'); // sessionStorage
    if (!token) {
      setError('');
      return;
    }
    
    setSubmitting(true);
    setError('');

    try {
      const response = await projectAPI.create({
        projectName: name,
        description: description,
        projectOwner: user?.username || sessionStorage.getItem('username'), // sessionStorage
        startTime: startDate ? `${startDate} 00:00:00` : null,
        endTime: endDate ? `${endDate} 23:59:59` : null
      });

      

      if (response.ok) {
        
        // 
        navigate('/projects', { 
          state: { shouldRefresh: true, message: '' }
        });
      } else {
        console.error(':', response.error);
        setError(response.error?.message || '');
      }
    } catch (error) {
      console.error(':', error);
      setError(': ' + error.message);
    } finally {
      setSubmitting(false);
    }
  };

  return (
    <div className="project-create-container">
      <div className="project-create-header">
        <div className="header-content">
          <h1 className="page-title">
            <span className="title-icon"></span>
            
          </h1>
          <p className="page-subtitle"></p>
        </div>
      </div>

      <div className="project-create-card">
        {error && (
          <div className="error-banner">
            <span className="error-icon"></span>
            {error}
          </div>
        )}

        <form className="project-form" onSubmit={handleSubmit}>
          <div className="form-section">
            <div className="form-group">
              <label className="form-label">
                
                <span className="required-mark">*</span>
              </label>
              <div className="input-wrapper">
                <input 
                  type="text" 
                  value={name} 
                  onChange={(e) => setName(e.target.value)} 
                  placeholder="" 
                  className="form-input"
                />
              </div>
              {errors.name && <div className="error-message">{errors.name}</div>}
            </div>

            <div className="form-group">
              <label className="form-label"></label>
              <div className="input-wrapper">
                <textarea
                  rows={4}
                  value={description}
                  onChange={(e) => setDescription(e.target.value)}
                  placeholder=""
                  className="form-textarea"
                />
              </div>
            </div>

            <div className="time-section">
              <div className="form-group">
                <label className="form-label"></label>
                <div className="date-input-wrapper">
                  <input 
                    type="date" 
                    value={startDate} 
                    onChange={(e) => setStartDate(e.target.value)} 
                    className="form-input date-input"
                  />
                  <span className="input-icon"></span>
                </div>
              </div>

              <div className="form-group">
                <label className="form-label"></label>
                <div className="date-input-wrapper">
                  <input 
                    type="date" 
                    value={endDate} 
                    onChange={(e) => setEndDate(e.target.value)} 
                    className="form-input date-input"
                  />
                  <span className="input-icon"></span>
                </div>
                {errors.time && <div className="error-message">{errors.time}</div>}
              </div>
            </div>
          </div>

          <div className="form-actions">
            <button 
              type="button" 
              onClick={() => navigate("/projects")} 
              disabled={submitting}
              className="btn btn-secondary"
            >
              <span className="btn-icon"></span>
              
            </button>
            <button 
              type="submit" 
              disabled={submitting}
              className="btn btn-primary"
            >
              <span className="btn-icon">
                {submitting ? "" : ""}
              </span>
              {submitting ? "..." : ""}
            </button>
          </div>
        </form>
      </div>
    </div>
  )
}





