import React, { useState, useEffect } from 'react';
import { scoreAPI, notificationAPI } from '../../utils/api';
import './ScoreModal.css';

const ScoreModal = ({ file, onClose, user, onScoreSubmitted }) => {
  const [score, setScore] = useState(5);
  const [password, setPassword] = useState('');
  const [submitting, setSubmitting] = useState(false);
  const [contractInfo, setContractInfo] = useState(null);
  const [userScore, setUserScore] = useState(0);
  const [hasScored, setHasScored] = useState(false);

  // 
  const isDetailsView = file.isDetailsView || file.username === user.username;

  useEffect(() => {
    if (file.address && file.address !== 'undefined') {
      loadContractInfo();
      // 
      if (!isDetailsView) {
        loadUserScore();
      }
    } else {
      console.warn(':', file.address);
    }
  }, [file.address, isDetailsView]);

  const loadContractInfo = async () => {
    try {


      const result = await scoreAPI.getContract(file.address);


      if (result.ok) {
        setContractInfo(result.data);

      } else {
        console.error(':', result.error?.message);
        // API
        setContractInfo({
          averageScore: 0,
          scoreCount: 0,
          contributionPoints: 0,
          timeFactor: 0,
          subtaskEndtime: 0
        });
      }
    } catch (error) {
      console.error(':', error);
      // API
      setContractInfo({
        averageScore: 0,
        scoreCount: 0,
        contributionPoints: 0,
        timeFactor: 0,
        subtaskEndtime: 0
      });
    }
  };

  const loadUserScore = async () => {
    try {
      // sessionStorage
      const userAddress = user.address || sessionStorage.getItem('address') || sessionStorage.getItem('userAddress');
      if (!userAddress) {
        console.warn('');
        return;
      }

      const result = await scoreAPI.getUserScore(file.address, userAddress);
      if (result.ok) {
        setUserScore(result.data.score);
        setHasScored(result.data.hasScored);
      }
    } catch (error) {
      console.error(':', error);
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (!password.trim()) {
      alert('');
      return;
    }

    // 
    if (file.username === user.username) {
      alert('');
      return;
    }

    try {
      setSubmitting(true);
      const result = await scoreAPI.submit({
        contractAddress: file.address,
        score: score,
        password: password.trim()
      });

      if (result.ok) {
        alert('');
        setHasScored(true);
        setUserScore(score);
        loadContractInfo(); // 

        // 
        try {
          await notificationAPI.markAsReadByFileId(file.id);

        } catch (error) {
          console.error(':', error);
        }

        if (onScoreSubmitted) {
          onScoreSubmitted();
        }
        onClose();
      } else {
        alert(`: ${result.error?.message || ''}`);
      }
    } catch (error) {
      console.error(':', error);
      alert(`: ${error.message}`);
    } finally {
      setSubmitting(false);
    }
  };

  const handleDownload = async () => {
    try {
      const response = await fetch(`/api/files/download`, {
        method: 'POST',
        headers: {
          'Authorization': 'Bearer ' + sessionStorage.getItem('token'),
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({
          filename: file.fileName
        })
      });

      if (response.ok) {
        const blob = await response.blob();
        const url = window.URL.createObjectURL(blob);
        const a = document.createElement('a');
        a.href = url;
        a.download = file.originalName;
        document.body.appendChild(a);
        a.click();
        window.URL.revokeObjectURL(url);
        document.body.removeChild(a);
      } else {
        alert('');
      }
    } catch (error) {
      console.error(':', error);
      alert('');
    }
  };

  const formatDate = (timestamp) => {
    if (!timestamp) return "";

    let date;
    // timestamp
    if (typeof timestamp === 'number') {
      // 
      if (timestamp > 1000000000000) {
        date = new Date(timestamp);
      } else {
        // 
        date = new Date(timestamp * 1000);
      }
    } else {
      // 
      const time = parseInt(timestamp);
      if (isNaN(time)) {
        // 
        date = new Date(timestamp);
      } else {
        // 
        if (time > 1000000000000) {
          date = new Date(time);
        } else {
          date = new Date(time * 1000);
        }
      }
    }

    if (isNaN(date.getTime())) return "";
    return date.toLocaleString('zh-CN');
  };

  return (
    <div className="modal-overlay" onClick={onClose}>
      <div className="score-modal" onClick={e => e.stopPropagation()}>
        <div className="modal-header">
          <h3>{isDetailsView ? ' ' : ' '}</h3>
          <button className="close-btn" onClick={onClose}></button>
        </div>

        <div className="modal-body">
          {/*  */}
          <div className="file-info">
            <div className="file-icon"></div>
            <div>
              <h4>{file.originalName}</h4>
              <p>: {file.username}</p>
              <p>: {formatDate(file.uploadTime)}</p>
              {file.description && (
                <p>: {file.description}</p>
              )}
            </div>
          </div>

          {/*  -  */}
          {file.address && file.address !== 'undefined' && (
            <div className="contract-info-section">
              <div className="info-grid">
                {contractInfo ? (
                  <>
                    <div className="info-item">
                      <span className="info-label">:</span>
                      <span className="info-value">{contractInfo.averageScore || 0}/10</span>
                    </div>
                    <div className="info-item">
                      <span className="info-label">:</span>
                      <span className="info-value">{contractInfo.scoreCount || 0}</span>
                    </div>
                    <div className="info-item">
                      <span className="info-label">:</span>
                      <span className="info-value">{contractInfo.contributionPoints || 0}</span>
                    </div>
                    <div className="info-item">
                      <span className="info-label">:</span>
                      <span className="info-value">{contractInfo.timeFactor || 0}/1000</span>
                    </div>
                    <div className="info-item">
                      <span className="info-label">:</span>
                      <span className="info-value">{formatDate(contractInfo.subtaskEndtime)}</span>
                    </div>
                  </>
                ) : (
                  <div className="info-item">
                    <span className="info-label">:</span>
                    <span className="info-value">...</span>
                  </div>
                )}
                <div className="info-item">
                  <span className="info-label">:</span>
                  <span className="info-value contract-address">{file.address}</span>
                </div>
              </div>
            </div>
          )}

          {/*  */}
          {isDetailsView ? (
            <div className="details-section">
              <div className="download-section">
                <button
                  className="download-btn"
                  onClick={handleDownload}
                >
                   
                </button>
              </div>

              <div className="scored-section">
                <div className="scored-info">
                  <div className="scored-icon"></div>
                  <div className="scored-text">
                    <h4></h4>
                    <p>: {file.fileSize ? `${(file.fileSize / 1024).toFixed(2)} KB` : ''}</p>
                    <p>: {file.fileType || ''}</p>
                    <p>: {file.fileHash || ''}</p>
                  </div>
                </div>
              </div>

              <div className="modal-actions">
                <button type="button" onClick={onClose}></button>
              </div>
            </div>
          ) : (
            /*  */
            <>
              {!hasScored ? (
                <form onSubmit={handleSubmit}>
                  <div className="score-section">
                    <label>(1-10):</label>
                    <div className="score-display">
                      <span className="score-value">{score}</span>
                    </div>
                    <div className="score-slider">
                      <input
                        type="range"
                        min="1"
                        max="10"
                        value={score}
                        onChange={(e) => setScore(parseInt(e.target.value))}
                        className="slider"
                      />
                      <div className="slider-labels">
                        <span>1</span>
                        <span>10</span>
                      </div>
                    </div>
                  </div>

                  <div className="password-section">
                    <label>:</label>
                    <input
                      type="password"
                      value={password}
                      onChange={(e) => setPassword(e.target.value)}
                      placeholder=""
                      required
                    />
                  </div>

                  <div className="modal-actions">
                    <button type="button" onClick={onClose}></button>
                    <button type="submit" disabled={submitting}>
                      {submitting ? '...' : ''}
                    </button>
                  </div>
                </form>
              ) : (
                <div className="scored-section">
                  <div className="scored-info">
                    <div className="scored-icon"></div>
                    <div className="scored-text">
                      <h4></h4>
                      <p>: {userScore}/10</p>
                    </div>
                  </div>
                  <div className="modal-actions">
                    <button type="button" onClick={onClose}></button>
                  </div>
                </div>
              )}
            </>
          )}
        </div>
      </div>
    </div>
  );
};

export default ScoreModal;
