import React, { useState, useEffect } from 'react';
import api, { projectMemberAPI, scoreAPI } from '../../utils/api';
import './MemberManagement.css';

const MemberManagement = ({ projectId, user, isProjectOwner }) => {
  const [members, setMembers] = useState([]);
  const [loading, setLoading] = useState(true);
  const [showAddModal, setShowAddModal] = useState(false);
  const [newMember, setNewMember] = useState({ username: '', role: '' });
  const [memberStats, setMemberStats] = useState({});

  useEffect(() => {
    loadMembers();
    loadMemberStats();
  }, [projectId]);

  // 
  useEffect(() => {
    const handleContributionUpdate = () => {
  
      loadMembers();
      loadMemberStats();
    };

    window.addEventListener('contributionUpdated', handleContributionUpdate);
    
    return () => {
      window.removeEventListener('contributionUpdated', handleContributionUpdate);
    };
  }, []);

  const loadMembers = async () => {
    try {

      const response = await projectMemberAPI.list(projectId);
      
      if (response.ok && response.data) {
        setMembers(response.data);
        
      } else {
        console.error(':', response.error);
        setMembers([]); // undefined
      }
    } catch (error) {
      console.error(':', error);
      setMembers([]); // undefined
    } finally {
      setLoading(false);
    }
  };

  const loadMemberStats = async () => {
    try {

      const response = await scoreAPI.getProjectContributions(projectId);
      
      if (response.ok && response.data) {
        // usernamekey
        const statsObj = {};
        response.data.forEach(member => {
          statsObj[member.username] = {
            score: member.score,
            tasks: member.tasks
          };
        });
        setMemberStats(statsObj);
        
      } else {
        console.error(':', response.error);
        setMemberStats({});
      }
    } catch (error) {
      console.error(':', error);
      setMemberStats({});
    }
  };

  const handleAddMember = async (e) => {
    e.preventDefault();
    if (!newMember.username.trim()) {
      alert('');
      return;
    }

    try {
      const response = await projectMemberAPI.add({
        projectId: parseInt(projectId),
        username: newMember.username.trim(),
        role: newMember.role
      });

             if (response.ok) {
         setMembers([...members, response.data]);
         setNewMember({ username: '', role: '' });
         setShowAddModal(false);
         alert('');
         loadMembers(); // 
       } else {
        alert(response.error?.message || '');
      }
    } catch (error) {
      console.error(':', error);
      alert('');
    }
  };

  const handleUpdateMemberRole = async (username, newRole) => {
    try {
      const response = await projectMemberAPI.update(projectId, {
        username,
        role: newRole
      });

      if (response.ok) {
        setMembers(members.map(member => 
          member.username === username 
            ? { ...member, role: newRole }
            : member
        ));
        alert('');
      } else {
        alert(response.error?.message || '');
      }
    } catch (error) {
      console.error(':', error);
      alert('');
    }
  };

  const handleRemoveMember = async (username) => {
    if (!window.confirm(` ${username} `)) return;

    try {
      const response = await projectMemberAPI.delete(projectId, {
        username
      });

      if (response.ok) {
        setMembers(members.filter(member => member.username !== username));
        alert('');
      } else {
        alert(response.error?.message || '');
      }
    } catch (error) {
      console.error(':', error);
      alert('');
    }
  };

  const getRoleColor = (role) => {
    const colors = {
      '': '#dc2626', // 
      '': '#ea580c', // 
      '': '#2563eb', // 
      '': '#7c3aed', // 
      '': '#059669', // 
      'UI/UX': '#db2777', // 
      '': '#0891b2', // 
      '': '#65a30d', // 
      '': '#9333ea', // 
      '': '#ea580c', // 
      '': '#6b7280', // 
      '': '#f59e0b'
    };
    return colors[role] || '#6366f1'; // 
  };

  const getRoleText = (role) => {
    // 
    if (['', '', '', '', '', '', 'UI/UX', '', '', '', '', ''].includes(role)) {
      return role;
    }
    // 
    const texts = {
      'leader': '',
      'project_manager': '',
      'tech_lead': '',
      'frontend_dev': '',
      'backend_dev': '',
      'fullstack_dev': '',
      'ui_ux_designer': 'UI/UX',
      'product_manager': '',
      'test_engineer': '',
      'devops_engineer': '',
      'data_analyst': '',
      'documentation': ''
    };
    return texts[role] || '';
  };

  if (loading) {
    return <div className="loading">...</div>;
  }

  return (
    <div className="member-management">
      <div className="member-header">
        <h2> </h2>
        {isProjectOwner && (
          <button 
            className="add-member-btn"
            onClick={() => setShowAddModal(true)}
          >
            + 
          </button>
        )}
      </div>

                           {/*  */}
        <div className="member-stats-section">
          <h3> </h3>
          <div className="contribution-leaderboard">
            {members && members.length > 0 ? 
              members
                .map(member => {
                  if (!member || !member.username) return null;
                  const stats = memberStats[member.username] || { score: 0, tasks: 0 };
                  // contributionPoint
                  return { 
                    ...member, 
                    stats: {
                      ...stats,
                      score: member.contributionPoint || 0 // 
                    }
                  };
                })
                .filter(member => member !== null)
                .sort((a, b) => (b.stats.score || 0) - (a.stats.score || 0))
                .map((member, index) => (
                  <div key={member.username} className={`leaderboard-item rank-${index + 1}`}>
                    <div className="rank-badge">
                      {index + 1}
                    </div>
                    <div className="member-avatar">
                      {member.username.charAt(0).toUpperCase()}
                    </div>
                    <div className="member-info">
                      <h4>{member.username}</h4>
                      <span className="member-role" style={{ background: getRoleColor(member.role) }}>
                        {getRoleText(member.role)}
                      </span>
                    </div>
                    <div className="contribution-score">
                      <div className="score-number">{member.stats.score || 0}</div>
                      <div className="score-label"></div>
                    </div>
                    <div className="contribution-details">
                      <div className="detail-item">
                        <span className="detail-label"></span>
                        <span className="detail-value">{member.stats.tasks || 0}</span>
                      </div>
                      <div className="detail-item">
                        <span className="detail-label"></span>
                        <span className="detail-value">{member.stats.score > 0 ? Math.round(member.stats.score / Math.max(member.stats.tasks, 1)) : 0}</span>
                      </div>
                    </div>
                  </div>
                ))
              : <p className="no-data"></p>
            }
          </div>
        </div>

             {/*  */}
       <div className="member-list-section">
         <h3> </h3>
         <div className="member-list">
           {members && members.length > 0 ? members.map(member => {
             if (!member || !member.username) return null; // 
             return (
                               <div key={member.username} className={`member-item ${member.username === user.username ? 'current-user' : ''}`}>
                  <div className="member-info">
                    <div className="member-avatar">
                      {member.username.charAt(0).toUpperCase()}
                    </div>
                                                            <div className="member-details">
                      <h4>{member.username}</h4>
                    </div>
                  </div>
                  
                                     <div className="member-actions">
                     {/*  */}
                     <div className="role-display">
                       <span 
                         className="member-role"
                         style={{ background: getRoleColor(member.role) }}
                       >
                         {getRoleText(member.role)}
                       </span>
                     </div>
                     {/*  */}
                     {isProjectOwner && member.username !== user.username ? (
                       <button
                         className="remove-btn"
                         onClick={() => handleRemoveMember(member.username)}
                       >
                         
                       </button>
                     ) : (
                       <div className="remove-btn-placeholder"></div>
                     )}
                   </div>
                </div>
             );
           }) : <p></p>}
         </div>
       </div>

      {/*  */}
      {showAddModal && (
        <div className="modal-overlay" onClick={() => setShowAddModal(false)}>
          <div className="modal" onClick={e => e.stopPropagation()}>
            <div className="modal-header">
              <h3></h3>
              <button 
                className="close-btn"
                onClick={() => setShowAddModal(false)}
              >
                
              </button>
            </div>
            
            <form onSubmit={handleAddMember} className="modal-body">
              <div className="form-group">
                <label></label>
                <input
                  type="text"
                  value={newMember.username}
                  onChange={(e) => setNewMember({...newMember, username: e.target.value})}
                  placeholder=""
                  required
                />
              </div>
              
              <div className="form-group">
                <label></label>
                <select
                  value={newMember.role}
                  onChange={(e) => setNewMember({...newMember, role: e.target.value})}
                >
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value="UI/UX">UI/UX</option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                </select>
              </div>
              
              <div className="modal-actions">
                <button type="button" onClick={() => setShowAddModal(false)}>
                  
                </button>
                <button type="submit">
                  
                </button>
              </div>
            </form>
          </div>
        </div>
      )}
    </div>
  );
};

export default MemberManagement;
