import React, { useState, useEffect } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import api from '../../utils/api';
import { milestoneAPI } from '../../utils/api';
import SubtaskManagement from '../Subtask/SubtaskManagement';
import './MilestoneDetail.css';

const MilestoneDetail = ({ user }) => {
  const { projectId, milestoneId } = useParams();
  const navigate = useNavigate();
  const [milestone, setMilestone] = useState(null);
  const [project, setProject] = useState(null);
  const [subtasks, setSubtasks] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');
  
  // 
  const [showEditModal, setShowEditModal] = useState(false);
  const [updating, setUpdating] = useState(false);
  const [editForm, setEditForm] = useState({
    title: '',
    description: '',
    startDate: '',
    endDate: '',
  });

  useEffect(() => {
    if (projectId && milestoneId) {
      loadMilestoneData();
    }
  }, [projectId, milestoneId]);

  // 
  useEffect(() => {

    
    const handleSubtaskChange = (event) => {
      
      // milestoneId
      if (event.detail.milestoneId == milestoneId) {
        loadMilestoneData();
      }
    };

    // 
    window.addEventListener('subtaskStatusChanged', handleSubtaskChange);
    
    
    return () => {
      window.removeEventListener('subtaskStatusChanged', handleSubtaskChange);
      
    };
  }, [milestoneId]);

  const loadMilestoneData = async () => {
    try {
      setLoading(true);
      
      // 
      const [milestoneRes, projectRes, subtasksRes] = await Promise.all([
        api.get(`/api/milestones/getMilestoneDetail/${milestoneId}`), // 
        api.get(`/api/projectManager/getProjectDetail/${projectId}`),  // 
        api.get(`/api/subtasks/getSubtaskList/${milestoneId}`)
      ]);

      if (milestoneRes.data.success) {
        const milestoneData = milestoneRes.data.data;
        setMilestone(milestoneData);
        // 
        setEditForm({
          title: milestoneData.title || '',
          description: milestoneData.description || '',
          startDate: milestoneData.startTime ? milestoneData.startTime.split(' ')[0] : '',
          endDate: milestoneData.endTime ? milestoneData.endTime.split(' ')[0] : '',
        });
      } else {
        setError('');
      }

      if (projectRes.data.success) {
        setProject(projectRes.data.data);
      }

      let subtasksData = [];
      if (subtasksRes.data.success) {
        subtasksData = subtasksRes.data.data || [];
        setSubtasks(subtasksData);
      }

      // 
      if (milestoneRes.data.success && subtasksData.length > 0) {
        const completedSubtasks = subtasksData.filter(subtask => subtask.status === 'completed');
        const shouldBeCompleted = completedSubtasks.length === subtasksData.length;
        
        // completedcompleted
        if ((shouldBeCompleted && milestoneRes.data.data.status !== 'completed') ||
            (!shouldBeCompleted && milestoneRes.data.data.status === 'completed')) {
          
          // 
          const newStatus = shouldBeCompleted ? 'completed' : 'in_progress';
          try {
            const updateRes = await api.put(`/api/milestones/updateMilestoneStatus/${milestoneId}`, {
              status: newStatus
            });
            
            if (updateRes.data.success) {
              // 
              setMilestone(prev => ({
                ...prev,
                status: newStatus
              }));
      
            }
          } catch (updateError) {
            console.error(':', updateError);
          }
        }
      }
    } catch (error) {
      console.error(':', error);
      setError('');
    } finally {
      setLoading(false);
    }
  };

  const getStatusColor = (status) => {
    const colors = {
      'in_progress': '#3b82f6',
      'completed': '#10b981',
      'overdue': '#ef4444'
    };
    return colors[status] || colors['in_progress'];
  };

  const getStatusText = (status) => {
    const texts = {
      'in_progress': '',
      'completed': '',
      'overdue': ''
    };
    return texts[status] || '';
  };

  const formatDate = (dateString) => {
    if (!dateString) return '';
    try {
      // MySQL
      let date;
      if (typeof dateString === 'number') {
        // 
        date = new Date(dateString * 1000);
      } else {
        // 
        date = new Date(dateString);
      }
      
      if (isNaN(date.getTime())) return '';
      
      return date.toLocaleDateString('zh-CN', {
        year: 'numeric',
        month: '2-digit',
        day: '2-digit'
      });
    } catch (error) {
      console.error(':', error, dateString);
      return '';
    }
  };

  const calculateProgress = () => {
    // 
    if (subtasks.length === 0) {
      // 
      if (milestone?.status === 'completed') return 100;
      if (milestone?.status === 'in_progress') return 0;
      return 0;
    }
    
    // 
    const completedSubtasks = subtasks.filter(subtask => subtask.status === 'completed');
    const progressPercentage = (completedSubtasks.length / subtasks.length) * 100;
    
    return Math.round(progressPercentage);
  };

  // 
  const [animatedProgress, setAnimatedProgress] = useState(0);

  useEffect(() => {
    const targetProgress = calculateProgress();
    const duration = 1000; // 1
    const steps = 60;
    const increment = targetProgress / steps;
    const stepDuration = duration / steps;
    
    let currentProgress = 0;
    const timer = setInterval(() => {
      currentProgress += increment;
      if (currentProgress >= targetProgress) {
        currentProgress = targetProgress;
        clearInterval(timer);
      }
      setAnimatedProgress(currentProgress);
    }, stepDuration);

    return () => clearInterval(timer);
  }, [milestone?.status, subtasks]);

  // 
  const toDateTime = (dateStr) => {
    if (!dateStr) return null;
    return `${dateStr} 00:00:00`;
  };

  // 
  const handleEdit = () => {
    setShowEditModal(true);
  };

  // 
  const handleUpdate = async (e) => {
    e.preventDefault();
    if (!editForm.title.trim()) {
      alert("");
      return;
    }

    const payload = {
      title: editForm.title.trim(),
      description: editForm.description.trim(),
      startDate: toDateTime(editForm.startDate),
      endDate: toDateTime(editForm.endDate),
    };

    setUpdating(true);
    try {
      const response = await milestoneAPI.update(milestoneId, payload);
      if (response.ok) {
        setShowEditModal(false);
        // 
        await loadMilestoneData();
        alert('');
      } else {
        alert(response.error?.message || "");
      }
    } catch (error) {
      console.error(':', error);
      alert('');
    } finally {
      setUpdating(false);
    }
  };

  // 
  const resetEditForm = () => {
    if (milestone) {
      setEditForm({
        title: milestone.title || '',
        description: milestone.description || '',
        startDate: milestone.startTime ? milestone.startTime.split(' ')[0] : '',
        endDate: milestone.endTime ? milestone.endTime.split(' ')[0] : '',
      });
    }
  };

  if (loading) {
    return <div className="loading">...</div>;
  }

  if (error) {
    return <div className="error">{error}</div>;
  }

  if (!milestone) {
    return <div className="error"></div>;
  }

  const isProjectOwner = project?.projectOwner === user.username;

  return (
    <div className="milestone-detail">
      {/*  */}
      <div className="milestone-header">
        <div className="header-content">
          <div className="milestone-info">
            <div className="breadcrumb">
              <span 
                className="breadcrumb-item"
                onClick={() => navigate(`/project/${projectId}`)}
              >
                {project?.projectName}
              </span>
              <span className="breadcrumb-separator"></span>
              <span className="breadcrumb-current"></span>
            </div>
            
            <div className="milestone-title-section">
              <div className="milestone-icon"></div>
              <div>
                <h1 className="milestone-title">{milestone.title}</h1>
                <div className="milestone-badges">
                  <span 
                    className="status-badge" 
                    style={{ background: getStatusColor(milestone.status) }}
                  >
                    {getStatusText(milestone.status)}
                  </span>
                  <span className="date-badge">
                    {formatDate(milestone.startTime)} - {formatDate(milestone.endTime)}
                  </span>
                </div>
              </div>
            </div>
            
            {milestone.description && (
              <p className="milestone-description">{milestone.description}</p>
            )}
          </div>
          
          <div className="milestone-actions">
            <button 
              className="btn-secondary"
              onClick={() => navigate(`/project/${projectId}`)}
            >
              
            </button>
            {isProjectOwner && (
              <button 
                className="btn-primary"
                onClick={handleEdit}
              >
                
              </button>
            )}
          </div>
        </div>
      </div>

      {/*  */}
      <div className="milestone-overview">
        {/*  */}
        <div className="overview-grid">
          <div className="overview-card">
            <h3> </h3>
            <div className="progress-section">
              <div className="progress-bar-large">
                <div 
                  className="progress-fill-large"
                  style={{ 
                    width: `${animatedProgress}%`,
                    background: getStatusColor(milestone.status)
                  }}
                ></div>
              </div>
              <div className="progress-text">{Math.round(animatedProgress)}% </div>
            </div>
          </div>

          <div className="overview-card">
            <h3> </h3>
            <div className="stats-grid">
              <div className="stat-item">
                <div className="stat-number">{subtasks.length}</div>
                <div className="stat-label"></div>
              </div>
              <div className="stat-item">
                <div className="stat-number">{subtasks.filter(s => s.status === 'completed').length}</div>
                <div className="stat-label"></div>
              </div>
              <div className="stat-item">
                <div className="stat-number">{subtasks.filter(s => s.status === 'in_progress').length}</div>
                <div className="stat-label"></div>
              </div>
              <div className="stat-item">
                <div className="stat-number">0</div>
                <div className="stat-label"></div>
              </div>
            </div>
          </div>

          <div className="overview-card">
            <h3> </h3>
            <div className="time-info">
              <div className="time-item">
                <span className="time-label">:</span>
                <span className="time-value">{formatDate(milestone.startTime)}</span>
              </div>
              <div className="time-item">
                <span className="time-label">:</span>
                <span className="time-value">{formatDate(milestone.endTime)}</span>
              </div>
            </div>
          </div>
        </div>

        {/*  */}
        <div className="milestone-sidebar">
          <h3 className="sidebar-title"> </h3>
          
          <div className="sidebar-section">
            <h4></h4>
            <div className="sidebar-info">
              <div className="sidebar-info-item">
                <span className="sidebar-label"></span>
                <span className="sidebar-value">{getStatusText(milestone.status)}</span>
              </div>
              <div className="sidebar-info-item">
                <span className="sidebar-label"></span>
                <span className="sidebar-value">{formatDate(milestone.startTime)}</span>
              </div>
              <div className="sidebar-info-item">
                <span className="sidebar-label"></span>
                <span className="sidebar-value">{formatDate(milestone.endTime)}</span>
              </div>
            </div>
          </div>

          <div className="sidebar-section">
            <h4></h4>
            <div className="sidebar-info">
              <div className="sidebar-info-item">
                <span className="sidebar-label"></span>
                <span className="sidebar-value">{project?.projectName || ''}</span>
              </div>
              <div className="sidebar-info-item">
                <span className="sidebar-label"></span>
                <span className="sidebar-value">{project?.projectOwner || ''}</span>
              </div>
            </div>
          </div>

          <div className="sidebar-section">
            <h4></h4>
            <div className="sidebar-info">
              <div 
                className="sidebar-info-item" 
                style={{ cursor: 'pointer' }}
                onClick={() => navigate(`/project/${projectId}`)}
              >
                <span className="sidebar-label"></span>
                <span className="sidebar-value"></span>
              </div>
              {isProjectOwner && (
                <div 
                  className="sidebar-info-item" 
                  style={{ cursor: 'pointer' }}
                  onClick={handleEdit}
                >
                  <span className="sidebar-label"></span>
                  <span className="sidebar-value"></span>
                </div>
              )}
            </div>
          </div>
        </div>
      </div>

      {/*  */}
      <div className="subtask-section">
        <SubtaskManagement 
          projectId={projectId}
          milestoneId={milestoneId}
          user={user}
          isProjectOwner={isProjectOwner}
          onSubtaskChange={loadMilestoneData}
        />
      </div>

      {/*  */}
      {showEditModal && (
        <div className="modal-overlay" onClick={() => setShowEditModal(false)}>
          <div className="modal" onClick={e => e.stopPropagation()}>
            <div className="modal-header">
              <h3></h3>
              <button 
                className="close-btn"
                onClick={() => setShowEditModal(false)}
              >
                
              </button>
            </div>
            
            <form onSubmit={handleUpdate} className="modal-body">
              <div className="form-group">
                <label> *</label>
                <input
                  type="text"
                  value={editForm.title}
                  onChange={(e) => setEditForm({...editForm, title: e.target.value})}
                  placeholder=""
                  required
                />
              </div>
              
              <div className="form-group">
                <label></label>
                <textarea
                  value={editForm.description}
                  onChange={(e) => setEditForm({...editForm, description: e.target.value})}
                  placeholder=""
                  rows={3}
                />
              </div>
              
              <div className="form-row">
                <div className="form-group">
                  <label></label>
                  <input
                    type="date"
                    value={editForm.startDate}
                    onChange={(e) => setEditForm({...editForm, startDate: e.target.value})}
                  />
                </div>
                <div className="form-group">
                  <label></label>
                  <input
                    type="date"
                    value={editForm.endDate}
                    onChange={(e) => setEditForm({...editForm, endDate: e.target.value})}
                  />
                </div>
              </div>
              
              <div className="modal-actions">
                <button type="button" onClick={() => setShowEditModal(false)}>
                  
                </button>
                <button type="submit" disabled={updating}>
                  {updating ? "..." : ""}
                </button>
              </div>
            </form>
          </div>
        </div>
      )}
    </div>
  );
};

export default MilestoneDetail;
